import React from 'react';
import Container from '../../../common/Container';
import Header from '../../../common/Header';
import Section from '../../../common/Section';

function AuthorPage() {
  return (
    <Container>
      <Header title="O autorze" />
      <Section
        title="Wojciech Knott"
        sectionBody={(
          <>
            Obecnie pracuj jako Fullstack Web Developer.
            Programowaniem zajmuj si od wielu lat. W tym czasie poznaem podstawy kilku
            jzykw programowania i technologii, ale to programowanie
            {' '}
            <strong>aplikacji webowych </strong>
            sprawia mi najwiksz frajd!
          </>
        )}
      />
    </Container>
  );
}

export default AuthorPage;
